import React, { useState } from "react";
import AuthButton from "../components/Buttons/AuthButton";
import AuthInput from "../components/Inputs/AuthInput";
import FooterLink from "../components/FooterLink";
import axios from "axios";
import { API_URL } from "../utils/constants";

function LoginScreen({setToken}) {

  const [formValues, setFormValues] = useState({
    username: "",
    password: "",
  });

  const [loading, setLoading] = useState(false);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormValues({ ...formValues, [name]: value });
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    setLoading(true);
    axios.post(`${API_URL}/auth/signin`, formValues)
    .then((response) => {
      
       const { data } = response;
       const { token } = data;
      setLoading(false);
      setToken(token);
    })
    .catch((error) => {
      console.log(error);
      setLoading(false);
    });
  }

  return (
    <div className="w-full h-screen flex items-center justify-center bg-gray-800">
      <form
        className="bg-gray-400 sm:w-110 lg:w-85 w-96 h-auto rounded-lg pt-8 pb-8 px-8 flex flex-col items-center"

         onSubmit={handleSubmit}
      >
       
        <label className=" font-semibold text-6xl mb-4 ">
          Insta<span className="font-semibold text-white text-opacity-100">Post</span>
        </label>
        <AuthInput
          type="text"
          name="username"
          placeholder="Email"
          onChange={handleChange}
        />
        <AuthInput
          type="password"
          name="password"
          placeholder="Password"
          onChange={handleChange}
        />
        <AuthButton loading={loading}>Login</AuthButton>
        <FooterLink
          label="Welcome to INSTAPOST"
        />
      </form>
    </div>
  );
}

export default LoginScreen;
