import React, { useState, useEffect } from "react";
import { HashRouter } from "react-router-dom";
import AdminRouter from "./routes/AdminRouter";
import UserRouter from "./routes/UserRouter";
import AuthRouter from "./routes/AuthRouter";
import jwt_decode from "jwt-decode";
import { API_URL } from "./utils/constants";
import axios from "axios";
import '../src/index.css';

function App() {
  const [token, setToken] = useState(localStorage.getItem("token") || null); //verifico si existe un token en local storage y si existe lo cargo
  const [type, setType] = useState(null);

  useEffect(() => {
    if (token) {
      localStorage.setItem("token", token); //guarda el token en el local storage
      const decoded = jwt_decode(token); //decodifico el token

      axios
        .get(`${API_URL}/auth/whoami`, {
          headers: { Authorization: `Bearer ${token}` },
        })
        .then((response) => {
          const { data } = response;
          const { role, username } = data;
          setType(role);
          localStorage.setItem("username", username);
        });
    } else {
      setType(null);
    }
  }, [token]);

  return (
    <HashRouter>
      {/* condicional de pantallas a cargar segun el tipo de usuario */}
      {type === "admin" && <AdminRouter setToken={setToken} type={type}/>}
      {type === "user" && <UserRouter setToken={setToken} type={type}/>}
      {!type && <AuthRouter setToken={setToken} />}
    </HashRouter>
  );
}

export default App;
